.blog {
  h2 {
    margin-top: 0;
    margin-bottom: 31px;
  }
  ul {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 30px;
    row-gap: 30px;
    padding-left: 0;

    @media (max-width: $tablet-max-width) {
      display: block;
      margin: 0 auto;
      width: max-content;
    }
  }

  li {
    &:nth-child(3) {
      grid-column: 2/-1;
      grid-row: 1/3;
    }

    &:not(:last-child) {
      @media (max-width: $tablet-max-width) {
        margin-bottom: 51px;
      }

      @media (max-width: $mobile-max-width) {
        margin-bottom: 40px;
      }
    }
  }
}

.blog__link {
  margin: 0 auto 64px;

  @media (max-width: $mobile-max-width) {
    margin-bottom: 53px;
  }
}
