@font-face {
  font-family: "Poppins";
  src: url("../fonts/poppins-regular.woff2") format("woff2"), url("../fonts/poppins-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Poppins";
  src: url("../fonts/poppins-medium.woff2") format("woff2"), url("../fonts/poppins-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Poppins";
  src: url("../fonts/poppins-bold.woff2") format("woff2"), url("../fonts/poppins-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Cormorant Garamond";
  src: url("../fonts/cormorant-garamond-semi-bold.woff2") format("woff2"), url("../fonts/cormorant-garamond-semi-bold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Cormorant Garamond";
  src: url("../fonts/cormorant-garamond-bold.woff2") format("woff2"), url("../fonts/cormorant-garamond-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Cormorant Garamond";
  src: url("../fonts/cormorant-garamond-bold-italic.woff2") format("woff2"), url("../fonts/cormorant-garamond-bold-italic.woff") format("woff");
  font-weight: 700;
  font-style: italic;
  font-display: swap;
}

html {
  box-sizing: border-box;
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0 none;
  clip-path: inset(100%);
}

.article {
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 570px;
  height: 300px;
}

.article time {
  display: block;
  margin-bottom: 12px;
  font-family: "Poppins", "Nimbus Sans L", "Helvetica", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  color: #9b9997;
}

.article h3 {
  margin: 0 auto 39px;
  width: 57%;
  font-family: "Cormorant Garamond", "Century Schoolbook L", "Georgia", serif;
  font-weight: 600;
  font-size: 26px;
  text-align: center;
  line-height: 33px;
}

.article a {
  margin: 0 auto;
}

.article img {
  width: 285px;
  height: 300px;
}

.article--large {
  position: relative;
  display: block;
  height: 630px;
}

.article--large img {
  width: 570px;
  height: 630px;
}

.article__column {
  padding-top: 67px;
  width: 285px;
  height: 300px;
  background-color: #f0eae4;
  text-align: center;
}

.article--large .article__column {
  position: absolute;
  bottom: 0;
  right: 0;
}

.article__image-block {
  overflow: hidden;
}

.blog h2 {
  margin-top: 0;
  margin-bottom: 31px;
}

.blog ul {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 30px;
  row-gap: 30px;
  padding-left: 0;
}

.blog li:nth-child(3) {
  grid-column: 2/-1;
  grid-row: 1/3;
}

.blog__link {
  margin: 0 auto 64px;
}

.card-product {
  font-family: "Poppins", "Nimbus Sans L", "Helvetica", "Arial", sans-serif;
}

.card-product img {
  margin-bottom: 13px;
  width: 270px;
  height: 284px;
}

.card-product h3 {
  margin-top: 0;
  margin-bottom: 5px;
  font-weight: 500;
  font-size: 16px;
  text-align: center;
}

.card-product p {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: bold;
  font-size: 14px;
  text-align: center;
}

.card-product a {
  color: inherit;
}

.card-product a:visited {
  color: inherit;
}

.card-product a:hover {
  opacity: 0.5;
  color: rgba(0, 0, 0, 0.5);
}

.card-product a:active {
  opacity: 0.2;
}

.control {
  padding: 0;
  border-style: none;
  background-color: transparent;
  cursor: pointer;
}

.control:disabled {
  opacity: 0.2;
}

.control--nojs {
  display: none;
}

.intro {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 11px;
  min-height: 463px;
  background-image: url("../img/intro/intro-desktop.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  font-family: "Cormorant Garamond", "Century Schoolbook L", "Georgia", serif;
  color: #ffffff;
}

.intro p {
  margin-top: 0;
  margin-bottom: 25px;
  font-weight: bold;
  font-size: 45px;
  text-transform: uppercase;
  letter-spacing: 0.03em;
}

.intro span {
  display: block;
  font-size: 38px;
  font-style: italic;
  font-weight: normal;
  text-align: center;
}

.link {
  display: block;
  width: max-content;
  font-family: "Poppins", "Nimbus Sans L", "Helvetica", "Arial", sans-serif;
  font-weight: 500;
  font-size: 16px;
  border-bottom: 1px solid #a87b62;
}

.link:visited {
  color: inherit;
}

.link:hover {
  color: #a87b62;
}

.link:active {
  color: inherit;
  opacity: 0.5;
}

.main-navigation {
  padding-top: 18px;
  border-top: 1px solid #f6f5f4;
}

.main-navigation__menu {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 0 auto;
  padding-left: 98px;
  padding-right: 98px;
  width: 100%;
  max-width: 1366px;
  font-weight: 500;
  font-size: 16px;
}

.main-navigation__menu li {
  margin-bottom: 10px;
}

.main-navigation__menu li:not(:last-child) {
  margin-right: 33px;
}

.main-navigation__about-company,
.main-navigation__login {
  display: none;
}

.new h2 {
  margin-top: 0;
  margin-bottom: 36px;
}

.new__link {
  margin: 0 auto 60px;
}

.new__control {
  position: absolute;
  top: 30px;
}

.new__control span {
  display: none;
}

.new__control svg {
  stroke: #a87b62;
}

.new__control--previous {
  left: 0;
}

.new__control--next {
  right: 0;
  transform: rotate(180deg);
}

.new__control:hover svg {
  stroke: #1d1613;
}

.new__control:active svg {
  opacity: 0.5;
}

.new__catalog {
  margin-bottom: 52px;
}

.new__pagination-list {
  margin: 0 auto;
  padding-left: 0;
  width: max-content;
}

.page-body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  margin: 0 auto;
  min-height: 100%;
  text-size-adjust: none;
  line-height: 130%;
}

.page-body--opened-modal {
  overflow: hidden;
}

.page-body--opened-modal::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 5;
}

.page-footer {
  font-family: "Poppins", "Nimbus Sans L", "Helvetica", "Arial", sans-serif;
  font-size: 16px;
  color: #ffffff;
  background-color: #000000;
}

.page-footer h2 {
  margin-top: 0;
  margin-bottom: 9px;
  font-family: "Cormorant Garamond", "Century Schoolbook L", "Georgia", serif;
  font-size: 34px;
  letter-spacing: 0.03em;
}

.page-footer p {
  margin-top: 0;
  margin-bottom: 44px;
  color: #9b9997;
}

.page-footer a {
  color: inherit;
}

.page-footer a:hover {
  color: #a87b62;
}

.page-footer a:active {
  color: inherit;
  opacity: 0.5;
}

.page-footer__container {
  display: grid;
  grid-template-columns: 500px 1fr max-content;
  column-gap: 88px;
  margin: 0 auto;
  padding-top: 90px;
  padding-bottom: 100px;
  padding-right: 98px;
  padding-left: 98px;
  width: 100%;
  max-width: 1366px;
}

.page-footer__subscribe {
  padding-bottom: 8px;
}

.page-footer__navigation {
  display: grid;
  grid-template-columns: max-content max-content;
  column-gap: 73px;
}

.page-footer__list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  padding-top: 6px;
  list-style: none;
  font-weight: 500;
}

.page-footer__list li {
  margin-bottom: 21px;
}

.page-header {
  margin-bottom: 11px;
  font-family: "Poppins", "Nimbus Sans L", "Helvetica", "Arial", sans-serif;
  color: #1d1613;
}

.page-header--nojs {
  background-color: #a87b62;
}

.page-header ul {
  list-style: none;
}

.page-header a:not(.page-header__logo) {
  color: inherit;
}

.page-header a:not(.page-header__logo):visited {
  color: inherit;
}

.page-header a:not(.page-header__logo):hover {
  border-bottom: 1px solid #a87b62;
}

.page-header a:not(.page-header__logo):active {
  border-style: none;
  opacity: 0.5;
}

.page-header__top-container {
  position: relative;
  display: flex;
  justify-content: space-between;
  margin: 0 auto 50px;
  padding-left: 98px;
  padding-right: 98px;
  padding-top: 30px;
  max-width: 1366px;
  font-weight: 500;
  font-size: 14px;
}

.page-header__button-menu {
  display: none;
}

.page-header__logo {
  position: absolute;
  top: 30px;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: 66px;
  height: 66px;
}

.page-header__logo svg {
  width: 66px;
  height: 66px;
  fill: #a87b62;
}

.page-header__user-menu {
  display: flex;
  flex-wrap: wrap;
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 12px;
  padding-left: 0;
  max-width: 45%;
}

.page-header__user-menu li:not(:last-child) {
  margin-right: 35px;
}

.page-header__cart svg {
  display: none;
}

.page-main__section-full {
  margin-bottom: 132px;
}

.page-main__section {
  margin: 0 auto 124px;
  padding-right: 98px;
  padding-left: 98px;
  width: 100%;
  max-width: 1366px;
  color: #1d1613;
}

.page-main__section h2 {
  font-family: "Cormorant Garamond", "Century Schoolbook L", "Georgia", serif;
  font-size: 34px;
  text-align: center;
}

.page-main__section ul {
  list-style: none;
  margin-top: 0;
}

.page-main__container {
  position: relative;
}

.pagination {
  font-family: "Poppins", "Nimbus Sans L", "Helvetica", "Arial", sans-serif;
  font-weight: 500;
  font-size: 16px;
}

.pagination ul {
  display: flex;
}

.pagination p {
  display: none;
}

.pagination__item {
  font: inherit;
  color: #9b9997;
  border-style: none;
  background-color: transparent;
  cursor: pointer;
}

.pagination__item:hover {
  color: #a87b62;
}

.pagination__item:active {
  border-style: none;
  opacity: 0.5;
}

.pagination__item--current {
  color: inherit;
}

.search input {
  padding-left: 10px;
  font: inherit;
  border-style: none;
  cursor: pointer;
}

.search input::-webkit-input-placeholder {
  color: #9b9997;
}

.search input::-moz-placeholder {
  color: #9b9997;
}

.search input:hover {
  opacity: 0.5;
}

.search input:focus {
  outline: 3px solid #a87b62;
}

.search__button {
  padding: 14px 0 0;
  border-style: none;
  background-color: transparent;
  cursor: pointer;
}

.search__button:hover {
  fill: #a87b62;
}

.search__button:active {
  opacity: 0.5;
}

.search__button svg {
  width: 14px;
  height: 14px;
}

.slider {
  overflow: hidden;
}

.slider__list {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  padding-left: 0;
}

.slider__list--nojs {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 30px;
  row-gap: 30px;
}

.slider__list li {
  flex: 1 0 auto;
  margin-right: 30px;
}

.social svg {
  margin-right: 10px;
  width: 15px;
  height: 15px;
  fill: currentColor;
}

.subscribe {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #ffffff;
}

.subscribe input {
  padding: 0;
  font-weight: 500;
  font-size: 14px;
  border-style: none;
  background-color: transparent;
}

.subscribe input::-webkit-input-placeholder {
  color: #9b9997;
}

.subscribe input::-moz-placeholder {
  color: #9b9997;
}

.subscribe input:hover {
  opacity: 0.5;
}

.subscribe input:focus {
  outline: 3px solid #a87b62;
}

.subscribe__button {
  padding: 0;
  border-style: none;
  background-color: transparent;
  font: inherit;
  color: #ffffff;
  cursor: pointer;
}

.subscribe__button:hover {
  color: #a87b62;
}

.subscribe__button:active {
  color: inherit;
  opacity: 0.5;
}

.subscribe__button span {
  margin-right: 7px;
}

.subscribe__button svg {
  width: 30px;
  height: 11px;
  stroke: currentColor;
  transform: rotate(180deg);
}

@media (max-width: 1023px) {
  .article {
    grid-template-columns: 339px 339px;
    width: 678px;
    overflow: hidden;
  }

  .article h3 {
    width: 50%;
  }

  .article img {
    width: 339px;
    object-fit: cover;
  }

  .article--large {
    position: static;
    display: grid;
    height: 300px;
  }

  .article--large img {
    height: 412px;
  }

  .article__column {
    width: 339px;
  }

  .article--large .article__column {
    position: static;
    grid-template-columns: 1fr 1fr;
  }

  .blog ul {
    display: block;
    margin: 0 auto;
    width: max-content;
  }

  .blog li:not(:last-child) {
    margin-bottom: 51px;
  }

  .card-product img {
    margin-bottom: 16px;
    width: 324px;
    height: 340px;
  }

  .intro {
    padding-top: 0;
    background-image: url("../img/intro/intro-tablet.jpg");
  }

  .intro p {
    padding-top: 4px;
  }

  .main-navigation {
    border-style: none;
  }

  .main-navigation a:hover {
    background-color: rgba(0, 0, 0, 0.15);
  }

  .main-navigation--opened {
    position: absolute;
    top: 172px;
    left: 0;
    overflow: scroll;
    padding-top: 20px;
    padding-left: 45px;
    padding-bottom: 80px;
    width: 100%;
    height: 100vh;
    background-color: #a87b62;
    z-index: 10;
  }

  .main-navigation--nojs {
    position: static;
  }

  .main-navigation--closed .main-navigation__menu {
    display: none;
  }

  .main-navigation--opened .main-navigation__menu,
  .main-navigation--nojs .main-navigation__menu {
    display: block;
    margin-bottom: 42px;
    padding: 0;
    font-size: 24px;
  }

  .main-navigation__menu li {
    margin-bottom: 0;
  }

  .main-navigation__menu li:not(:last-child) {
    margin-right: 0;
    margin-bottom: 20px;
  }

  .main-navigation--opened .main-navigation__about-company,
  .main-navigation--nojs .main-navigation__about-company {
    display: block;
    margin-top: 0;
    margin-bottom: 46px;
    padding-left: 0;
    font-size: 18px;
  }

  .main-navigation__about-company li:not(:last-child) {
    margin-bottom: 20px;
  }

  .main-navigation--opened .main-navigation__login,
  .main-navigation--nojs .main-navigation__login {
    display: block;
    font-size: 18px;
  }

  .new h2 {
    margin-bottom: 31px;
  }

  .new__link {
    margin-bottom: 52px;
  }

  .new__control svg {
    width: 36px;
    height: 15px;
  }

  .new__catalog {
    margin: 0 auto 42px;
  }

  .page-footer__container {
    grid-template-columns: 1fr max-content;
    padding-top: 50px;
    padding-bottom: 50px;
    padding-right: 150px;
    padding-left: 45px;
  }

  .page-footer__section {
    grid-column: 1/-1;
    margin-bottom: 40px;
  }

  .page-footer__subscribe {
    margin-right: 12px;
  }

  .page-footer__navigation {
    column-gap: 83px;
  }

  .page-header {
    margin: 0 auto 35px;
    padding-left: 45px;
    padding-right: 45px;
    padding-top: 30px;
    width: 100%;
    color: #ffffff;
  }

  .page-header--opened-menu {
    background-color: #a87b62;
  }

  .page-header__top-container {
    display: grid;
    grid-template-columns: max-content 1fr max-content;
    row-gap: 63px;
    margin-bottom: 0;
    padding-left: 0;
    padding-right: 0;
    padding-top: 25px;
  }

  .page-header--opened-menu .page-header__top-container {
    position: fixed;
    top: 0;
    left: 0;
    padding-left: 45px;
    padding-right: 45px;
    padding-top: 53px;
    width: 100%;
    background-color: #a87b62;
    z-index: 50;
  }

  .page-header--no-js .page-header__top-container {
    position: static;
  }

  .page-header__button-menu {
    display: block;
    padding: 0;
    width: 25px;
    height: 18px;
    border-style: none;
    background-color: transparent;
    cursor: pointer;
    stroke: #1d1613;
  }

  .page-header__button-menu:hover {
    stroke: #a87b62;
  }

  .page-header__button-menu:active {
    stroke: #1d1613;
    opacity: 0.5;
  }

  .page-header--nojs .page-header__button-menu {
    display: none;
  }

  .page-header--opened-menu .page-header__button-menu {
    stroke: #ffffff;
  }

  .page-header--opened-menu .page-header__button-menu:hover {
    stroke: #1d1613;
  }

  .page-header__search {
    display: none;
  }

  .page-header--opened-menu .page-header__search,
  .page-header--nojs .page-header__search {
    display: block;
    order: 1;
    grid-column: 1/-1;
  }

  .page-header__logo {
    top: 0;
  }

  .page-header--opened-menu .page-header__logo {
    top: 30px;
  }

  .page-header--opened-menu .page-header__logo svg {
    fill: #ffffff;
  }

  .page-header--nojs .page-header__logo svg {
    fill: #ffffff;
  }

  .page-header__user-menu {
    grid-column-start: 3;
    padding-top: 0;
  }

  .page-header__login-top {
    display: none;
  }

  .page-header__cart:hover svg {
    stroke: #a87b62;
  }

  .page-header__cart:active {
    opacity: 0.5;
  }

  .page-header__cart svg {
    display: block;
    width: 20px;
    height: 18px;
    fill: none;
    stroke: #1d1613;
  }

  .page-header__cart span {
    display: none;
  }

  .page-header--opened-menu .page-header__cart:hover svg,
  .page-header--nojs .page-header__cart:hover svg {
    stroke: #1d1613;
  }

  .page-header--opened-menu .page-header__cart svg,
  .page-header--nojs .page-header__cart svg {
    stroke: #ffffff;
  }

  .page-main__section-full {
    margin-bottom: 82px;
  }

  .page-main__section {
    margin-bottom: 73px;
    padding-right: 45px;
    padding-left: 45px;
  }

  .search--opened-menu,
  .search--nojs {
    position: relative;
  }

  .search--opened-menu input,
  .search--nojs input {
    width: 100%;
    height: 40px;
    border-radius: 21px;
    background-color: rgba(0, 0, 0, 0.15);
  }

  .search--opened-menu input::-webkit-input-placeholder,
  .search--nojs input::-webkit-input-placeholder {
    color: transparent;
  }

  .search--opened-menu input::-moz-placeholder,
  .search--nojs input::-moz-placeholder {
    color: transparent;
  }

  .search--opened-menu .search__button,
  .search--nojs .search__button {
    position: absolute;
    bottom: 9px;
    left: 12px;
    fill: #ffffff;
  }

  .search--opened-menu .search__button svg,
  .search--nojs .search__button svg {
    width: 16px;
    height: 16px;
  }

  .slider {
    width: 678px;
    margin: 0 auto;
  }

  .slider__list--nojs {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 1023px) and (min-resolution: 2dppx) {
  .intro {
    background-image: url("../img/intro/intro-tablet@2x.jpg");
  }
}

@media (max-width: 767px) {
  .article {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 512px;
  }

  .article h3 {
    width: 56%;
  }

  .article img {
    width: 290px;
    height: 256px;
  }

  .article--large img {
    height: 412px;
  }

  .article__column {
    padding-top: 45px;
    width: 290px;
    height: 256px;
  }

  .article__image-block {
    order: -1;
    height: 256px;
  }

  .blog li:not(:last-child) {
    margin-bottom: 40px;
  }

  .blog__link {
    margin-bottom: 53px;
  }

  .card-product {
    width: 130px;
  }

