.pagination {
  @include poppins;
  font-weight: 500;
  font-size: 16px;

  ul {
    display: flex;

    @media (max-width: $mobile-max-width) {
      display: none;
    }
  }

  p {
    display: none;

    @media (max-width: $mobile-max-width) {
      display: block;
      margin-top: 0;
      margin-bottom: 0;
      text-align: center;
    }
  }
}

.pagination__item {
  font: inherit;
  color: $grey;
  border-style: none;
  background-color: transparent;
  cursor: pointer;

  &:hover {
    color: $brown;
  }

  &:active {
    border-style: none;
    opacity: 0.5;
  }

  &--current {
    color: inherit;
  }
}

.pagination__current-page {
  @media (max-width: $mobile-max-width) {
    margin-right: 10px;
  }
}

.pagination__total-page {
  @media (max-width: $mobile-max-width) {
    margin-left: 10px;
  }
}
